/* BLOCKS */
.btn {
  display: inline-flex;
  padding: 10px 15px;
  border-radius: 3px;
  background-color: rgba(255, 255, 255, 0);
  color: #f2f5f6;
  text-transform: uppercase;
  border: none;
  cursor: pointer;
  transition: opacity 0.3s ease, background-color 0.3s ease, color 0.3s ease;
}
.btn:disabled {
  opacity: 0.5;
  pointer-events: none;
}
.btn--banner {
  background-color: #00b2ff;
  padding: 17px 27px;
}
@media (max-width: 767px) {
  .btn--banner {
    padding: 12px 0;
  }
}
.btn--order {
  border: 1px solid #f2f5f6;
  padding: 11px 16px;
}
.btn--more {
  border: 1px solid #00b2ff;
  color: #00b2ff;
  justify-content: center;
  min-width: 180px;
  padding: 14px 0;
}
.btn--more > span:last-child {
  display: none;
}
.btn--more.is-clicked > span:last-child {
  display: block;
}
.btn--more.is-clicked > span:first-child {
  display: none;
}
@media (hover: hover) {
  .btn--more:hover:not(.focus-visible) {
    background-color: #00b2ff;
    color: #ffffff;
  }
}
.btn--more.focus-visible:focus {
  background-color: #00b2ff;
  color: #ffffff;
}
@media (max-width: 767px) {
  .btn--more {
    min-width: 160px;
    padding: 8px;
  }
}
.btn--form {
  background-color: #041427;
  justify-content: center;
  width: 265px;
  padding: 16px 0 10px;
}
@media (max-width: 767px) {
  .btn--form {
    width: 100%;
  }
}
@media (hover: hover) {
  .btn:hover:not(.focus-visible) {
    opacity: 0.8;
    outline: none;
  }
}
.btn.focus-visible:focus {
  opacity: 0.8;
  outline: none;
}

.modal {
  position: fixed;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  z-index: 1000;
  display: flex;
  flex-direction: column;
  overflow-y: auto;
  -webkit-overflow-scrolling: touch;
  background-color: rgba(4, 24, 39, 0.8);
  transform: scale(1.2);
  visibility: hidden;
  opacity: 0;
  transition: opacity 0.6s cubic-bezier(0.55, 0, 0.1, 1), transform 0.6s cubic-bezier(0.55, 0, 0.1, 1), visibility 0.6s cubic-bezier(0.55, 0, 0.1, 1);
  pointer-events: none;
  -ms-overflow-style: none;
  scrollbar-width: none;
}
.modal::-webkit-scrollbar {
  display: none;
}
.modal--fit-content .modal__content {
  width: auto;
}
@media (max-width: 767px) {
  .modal--fit-content .modal__content {
    width: 100%;
  }
}
.modal--responsive .modal__content {
  width: 100%;
}
.modal--no-scale {
  transform: none;
}
.modal--preload {
  transition: none;
}

.modal__wrapper {
  position: relative;
  display: flex;
  flex-shrink: 0;
  justify-content: center;
  align-items: center;
  width: 100%;
  min-height: 100%;
  padding: 60px 40px;
}
@media (max-width: 767px) {
  .modal__wrapper {
    padding: 60px 16px;
  }
}

.modal__overlay {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  cursor: pointer;
}

.modal__content {
  position: relative;
  width: 500px;
  padding: 37px 40px 41px;
  background-color: #00b2ff;
}
@media (max-width: 767px) {
  .modal__content {
    width: 100%;
    padding: 20px;
  }
}

.modal__close-btn {
  position: absolute;
  top: 15px;
  right: 15px;
  z-index: 1;
  width: 40px;
  height: 40px;
  padding: 0;
  background: rgba(255, 255, 255, 0);
  border: none;
  cursor: pointer;
}

.modal__close-btn svg {
  display: block;
  margin: auto;
}

.modal.is-active {
  transform: scale(1);
  visibility: visible;
  opacity: 1;
  pointer-events: auto;
}

